.mainWrapper {
  height: 60px;
  width: 100vw;
  position: fixed;
  top: 0;
  left: 0;
  background-color: #1ca0dc;
  z-index: 2;
  color: white;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  padding: 0 1rem;
  box-shadow: rgba(0, 0, 0, 0.15) 1.95px 1.95px 2.6px;
}

.sideMenuWrapper {
  position: fixed;
  top: 0;
  width: 300px;
  height: 100vh;
  //background-color: #1ca0dc;
  background-color: white;
  z-index: 1;
  transition: all .5s;
  box-shadow: rgba(50, 50, 93, 0.25) 0px 6px 12px -2px, rgba(0, 0, 0, 0.3) 0px 3px 7px -3px;

  &_open {
    left: 0;
    transition: all .5s;
  }

  &_close {
    left: -300px;
    transition: all .5s;
  }
}

.iconContainer{
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  margin-right: 1rem;transition: all .5s;
  svg{
    font-size: 1.5rem;
  }
}

